@import "../common/styles/sass/variables";

.header {
    height: 80px;
    width: 100%;
    background-color: #252527;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: fixed;
    opacity: 0.9;
    z-index: 1;
    @media screen and (max-width: 750px) {
        justify-content: center;
    }
}